img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
@font-face {
  font-family: "ptsans";
  src: local("ptsans"), local("ptsans Regular"), url("../../fonts/ptsans.woff2") format("woff2"), url("../../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "ptsans";
  src: local("ptsans"), local("ptsans bold"), url("../../fonts/ptsansbold.woff2") format("woff2"), url("../../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.container {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding-left: 32px;
    padding-right: 31px;
  }
}
.page {
  height: 100%;
  font-family: "ptsans", "Arial", sans-serif;
  font-weight: 400;
}
.page__body {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  margin: 0;
}
.page-footer {
  height: 300px;
  background-color: tomato;
}
.main-nav {
  position: relative;
}
.main-nav__toggle {
  position: absolute;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.main-nav__item {
  border-top: 1px solid #9ac2db;
  border-bottom: 1px solid #678fa8;
}
.main-nave__link {
  display: block;
  background-color: #81B3D2;
  font-weight: 700;
  font-size: 18px;
  line-height: 40px;
  text-transform: uppercase;
  text-align: center;
  color: #FFFFFF;
}
.main-nav__item--logo {
  text-align: center;
}
